@use '../../foundation/_variable' as *;

.p-lineup {
    background-color: $color-bg-cream;
    padding: 3rem 0 4.4rem;
    .c-inner {
        @include breakpoint(small) {
        padding: 0 1rem;
        }
    }
    &__gridWrapper {
        margin-top: 4.4rem;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(5, 1fr);
        gap: 1.3rem;
    }
    &__gridItem {
        position: relative;
    }
    &__img::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(transparent 60%,  rgba(0, 0, 0, .7));
    }
    &__name {
        font-size: 2.1rem;
        color: $color-base;
        font-weight: bold;
        position: absolute;
        left: 0;
        bottom: 0;
        user-select: none;
        margin: 0 0 0.5rem 0.5rem;
        line-height: 1.1;
        letter-spacing: 0.01em;
        @include breakpoint(small) {
            font-size: 3.6vw;
        }
    }
}