@use '../../foundation/_variable' as *;

.c-btn {
    display: block;
    width: 52rem;
    max-width: 52rem;
    height: 12rem;
    font-size: 3.6rem;
    color: $color-base;
    background: $color-green;
    padding: 1rem 2rem;
    border-radius: 6rem;
    box-shadow: 0px 3px 6px $color-gray;
    text-align: center;
    margin: 0 auto;
    letter-spacing: -0.05em;
    font-family: $font-zenKaku;
    font-weight: 900;
    &:hover {
        opacity: 0.8;
        transition: all 0.3s;
    }
    @include breakpoint(small) {
        font-size: 6vw;
        height: 20vw;
        width: 86vw;
    }
}
