@use '../../foundation/_variable' as *;

.p-campaign {
    background-color: $color-bg-cream;
    padding-bottom: .5rem;
    &__title {
        width: 28rem;
        text-align: center;
        margin: 0 auto;
        padding-top: 4rem;
        font-size: 2.4rem;
        &Jp {
            border-bottom: 2px solid $color-font;
            font-weight: 900;
            font-family: $font-zenKaku;
        }
        &En {
            text-transform: uppercase;
            font-family: $font-din;
            letter-spacing: 0.1em;
            line-height: 1.1;
        }
    }
    &__heading {
        margin: 2.8rem auto 0;
        color: $color-base;
        font-size: 3.6rem;
        max-width: 46.6rem;
        height: 11rem;
        font-weight: 900;
        line-height: 1.2;
        letter-spacing: -0.1em;
        background: url("../../assets/img/campaign-heading-bg@2x-8.png");
        display: flex;
        align-items: center;
        justify-content: center;
        @include breakpoint(small) {
            font-size: 6vw;
        }
    }
    &__catch {
        margin: 2.8rem auto 0;
        max-width: 46.6rem;
        height: 6rem;
    }
    &__content {
        margin-top: 3.6rem;
        letter-spacing: -0.03em;
        line-height: 1.36;
        text-align: center;
        font-size: 3rem;
        font-weight: bold;
        @include breakpoint(small) {
            font-size: 5vw;
        }
    }
    &__chart {
        margin: 2.3rem auto 0;
    }
    &__note {
        font-size: 1.6rem;
        margin-top: 2.3rem;
        color: $color-black;
        font-weight: 500;
        line-height: 1.5;
        letter-spacing: -0.03em;
        text-align: left;
        :last-child {
            margin-top: 2.4rem;
        }
        @include breakpoint(small) {
            font-size: 1.1rem;
        }
    }
    &__list {
        margin: 2.6rem auto 3.5rem;
    }
    &__listItem {
        margin: 0 auto;
        width: 84%;
        object-fit: cover;
        object-position: top;
        & img {
            width: 100%;
        }
        @include breakpoint(small) {
            width: 100%;
        }
    }
}
