@use '../../foundation/_variable' as *;

.p-cta {
    padding-top: 3rem;
    background: linear-gradient($color-bg-cream, transparent 20%);
    &__bgBase {
        background: $color-base;
    }
    &__bgYellow {
        background: linear-gradient($color-bg-yellow,  transparent 20%);
    }
    &__heading img {
        margin: 0 auto;
        width: 27rem;
        height: 5rem;
    }
    &__btn {
        margin: 2.4rem auto;
        display: flex;
        align-items: center;
        justify-content: center;
        &::after {
            content: "";
            display: block;
            width: 3.4rem;
            height: 2.8rem;
            background: url("../../assets/img/btn-arrow.svg") center / contain no-repeat;
            margin-left: 2.4rem;
        }
        @include breakpoint(small) {
            justify-content: space-between;
            &::after {
                margin-left: 0;
                width: 5.6vw;
            }
        }
    }
    &__img img {
        width: 100%;
        height: 20rem;
        object-fit: cover;
        object-position: bottom;
    }
    &__img__short img {
        height: 15rem;
    }
    &__pt {
        padding-top: 8rem;
    }
}
