@use '../foundation/_variable' as *;

.l-footer {
    background-color: $color-base;
    &__content {
        display: flex;
        padding: 0 2rem;
        gap: 3rem;
        align-items: flex-end;
        justify-content: space-between;
        @include breakpoint(small) {
            gap: 0;
            flex-direction: column-reverse;
        }
    }
    &__logo {
        height: auto;
    }
    &__nav ul {
        display: flex;
        font-weight: bold;
        width: 14rem;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        margin-bottom: 2rem;
        font-size: 1.2rem;
        li {
            text-align: left;
        }
        @include breakpoint(small) {
            flex-direction: row;
            width: 30rem;
            gap: 2rem;
            margin-bottom: 0;
            font-size: 1.1rem;
        }
    }
    small {
        background-color: $color-green;
        font-weight: normal;
        font-size: 1.3rem;
        font-family: $font-din;
        color: $color-base;
        text-align: center;
        height: 4rem;
        display: block;
        line-height: 4rem;
    }
}