@use '../../foundation/_variable' as *;

.c-sectionTitle {
    margin: 0 auto;
    text-align: center;
    &__en {
        font-size: 2.1rem;
        font-family: $font-din;
        line-height: 1.2;
        letter-spacing: 0.1em;
        font-weight: 900;
        text-transform: uppercase;
        margin-bottom: 0.6rem;
    }
    &__jp {
        font-size: 6rem;
        font-weight: 900;
        display: inline-block;
        border-top: 2px solid $color-font;
        line-height: 1.25;
        @include breakpoint(small) {
            font-size: max(4.6rem, 10vw);
        }
    }
}