@use '../../foundation/_variable' as *;

.p-reason {
    .c-inner {
        padding: 0;
    }
    padding: 5rem 0;
    margin: 0 auto;
    background: $color-bg-yellow;
    &__wrapper {
        padding: 0 2rem;
    }
    &__list {
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
    }
    &__listCol {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1.8rem;
        margin-top: 1.8rem;  
        li {
            max-width: 29rem;
        }    
    }
    &__note {
        margin-top: 2rem;
        font-size: 1.5rem;
        line-height: 1.3;
        font-weight: bold;
        @include breakpoint(small) {
            font-size: 1.1rem;
        }
    }
}