@use '../../foundation/_variable' as *;

.p-faq {
    padding: 6.6rem 0 6.8rem;
    .c-inner {
        @include breakpoint(small) {
            padding: 0 1rem;
        }
    }
    &__title {
        text-align: center;
        margin-top: 5.6rem;
        font-size: 4.4rem;
        font-weight: 900;
        line-height: 1.1;
        @include breakpoint(small) {
            font-size: 8vw;
        }
    }
    &__accordion {
        margin-top: 5rem;
        dl {
            font-weight: 500;
            :first-child {
                margin-top: 0;
            }
        }
        dt {
            font-size: 2.1rem;
            font-weight: 500;
            min-height: 5.5rem;
            border: 0.75px solid $color-font;
            border-radius: 10px 0 0 0;
            display: flex;
            align-items: center;
            user-select: none;
            cursor: pointer;
            margin-top: 2.2rem;
            padding: 1.6rem 5.5rem 1.6rem 3rem;
            display: flex;
            position: relative;
            @include breakpoint(small) {
                font-size: 3.5vw;
                padding: 1rem 2.5rem 1rem 1rem;
                margin-top: 1.4rem;

            }
            ::after {
                content: "";
                display: block; 
                width: 1.6rem;
                height: 1.6rem;
                border-left: 2.5px solid $color-font;
                border-bottom: 2.5px solid $color-font;
                transform: rotate(-45deg) translateY(-50%);
                transition: transform .5s;
                position: absolute;
                right: 2.4rem;
                top: 50%;
                @include breakpoint(small) {
                    right: 1rem;
                    width: 1.2rem;
                    height: 1.2rem;
                }
            }
        }
        & dd {
            padding: 0 3.4rem;
            height: 0;
            line-height: 0;
            overflow: hidden;
            font-size: 1.8rem;
            transition: .4s;
            color: $color-font;
            opacity: 0;
            border: 0.75px solid transparent;
            border-top: none;
            background: $color-bg-cream;
            @include breakpoint(small) {
                padding: 0 1.6rem;
                font-size: max(1.2rem, 3vw);
            }
        }
        dt.is-active {
            & span::after {
                transform: rotate(135deg);
            }
        }
        dd.is-open {
            border: 0.75px solid $color-font;
            border-top: none;
            line-height: 1.5;
            opacity: 1;
            padding: 3.4rem;
            height: auto;
            @include breakpoint(small) {
                padding: 1.6rem;
            }
        }
    }
    &__number {
        font-family: $font-din;
        font-weight: bold;
        margin-right: 2rem;
        @include breakpoint(small) {
            margin-right: 1rem;
        }
    }
}