@use '../../foundation/_variable' as *;

.p-contact {
    padding: 6.6rem 0 7.7rem;
    text-align: center;
&__text {
    font-size: 2.4rem;
    font-weight: bold;
    @include breakpoint(small) {
        font-size: 4vw;
    }
}
&__phone {
    font-size: 5.8rem;
    line-height: 1;
    margin-top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: $font-din;
    &::before {
        content: "";
        display: block;
        width: 5.6rem;
        height: 5.6rem;
        background: url("../../assets/img/contact-phone.svg") center / contain no-repeat;
        margin-right: 1rem;
    }
    @include breakpoint(small) {
        font-size: max(4rem, 9.6vw);
    }
}
&__detail {
    font-size: 1.7rem;
    font-weight: bold;
    font-family: $font-hiragino;
    margin-top: 0.5rem;
    line-height: 1.2;
    @include breakpoint(small) {
        font-size: 4vw;
    }
}
&__btn {
    margin-top: 4rem;
    background: $color-orange;
}
}
