@use '../../foundation/_variable' as *;

.p-voices {
    padding: 9rem 0 8.8rem;
    background: url("../../assets/img/voices-bg@2x-80.jpg") center / contain;
    &__cardUnit {
        margin-top: 8rem;   
        display: flex;
        flex-direction: column;
        gap: 3.2rem;
    }
    &__card {
        padding: 2.3rem 1.6rem 2.3rem 1rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: $color-base;
        @include breakpoint(small) {
            flex-direction: column;
            align-items: flex-start;
            gap: 1.6rem;
            padding: 1.6rem;
        }
    }
    &__cardHeading {
        flex-basis: 50%;
        border-left: 15px solid $color-font;
        padding-left: 0.6rem;
        font-size: 2.4rem;
        line-height: 1.2;
        font-weight: 900;
        letter-spacing: -0.02em;
        p {
            height: 6.2rem;
        }
    }
    &__cardtext {
        flex-basis: 50%;
        font-size: 1.8rem;
        color: $color-font-black;
        line-height: 1.33;
        letter-spacing: -0.05em;
    }
    &__content {
        flex-basis: 50%;
        display: flex;
        flex-direction: column;
        gap: 3rem;
    }
    &__imgWrapper {
        flex-basis: 50%;
        text-align: center;
        margin: 0 auto;
    }
    &__img img {
        margin: 0 auto;
    }
}