@use '../../foundation/_variable' as *;

.p-shopping {
    padding: 8rem 0 7rem;
    background: $color-bg-yellow;
    &__intro {
        margin-top: 1.4rem;
        text-align: center;
        font-weight: bold;
        font-size: 2.1rem;
        line-height: 1.2;
        letter-spacing: 0.01em;
        @include breakpoint(small) {
            font-size: 1.7rem;
        }
    }
    &__content {
        margin-top: 5.3rem;
    }
}