$color-bg-cream: #fdf7ed;
$color-bg-yellow: #ffffcc;
$color-font: #663300;
$color-green: #009933;
$color-orange: #ff9900;
$color-yellow: yellow;
$color-red: red;
$color-black: #231815;
$color-font-black: #2F3232;
$color-darkgray: darkgray;
$color-gray: gray;

//font-family
$font-zenKaku: 'DIN Alternate', "zenkaku", "Arial", "Yu Gothic", "Meiryo", sans-serif;
$font-hiragino: "Helvetica", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
$font-din: 'DIN Alternate', "notosans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
$font-kozukaMincho: "Helvetica", "Kozuka Mincho Pro", "Arial", "Yu Gothic", "Meiryo", 'DIN Alternate', serif;


// responsive
@mixin breakpoint($point) {
	@if $point == small {
		@media (max-width: 576px) {
			@content;
		}
	}
	@if $point == medium {
		@media (max-width: 768px) {
			@content;
		}
	}
	@if $point == large {
		@media (max-width: 992px) {
			@content;
		}
	}
	@if $point == x-large {
		@media (max-width: 1200px) {
			@content;
		}
	}
	@if $point == xx-large {
		@media (max-width: 1400px) {
			@content;
		}
	}
}
