@use '../foundation/_variable' as *;

.l-header {
    width: 19.5rem;
    top: 0;
    z-index: 999;
    background-color: $color-base;
    position: relative;
    @include breakpoint(small) {
        width: max(15rem, 35vw);
    }
    &__content {
        padding: 3.2rem 2rem 1rem;
        display: block;
        width: 100%;
        height: auto;
        @include breakpoint(small) {
            padding: 1rem;
        }
    }
    &__logo {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
    }
}